import React, { useEffect } from 'react'
import { useSearchParams } from 'react-router-dom'
import LoadingScreen from '../app/pages/LoadingScreen';

const sendCode = (code) => {
    console.log('code', code)
    const channel = new BroadcastChannel('connect');
    if (code) {
        if(window.opener) window.opener.postMessage({error:false, code: code}, '*');
        else channel.postMessage(JSON.stringify({error:false, code: code}))
        window.close()
    }
}

const Authorization = () => {
    const [searchParams, setSearchParams] = useSearchParams();
    useEffect(() => {
        sendCode(searchParams.get("code"))
        return () => {
            sendCode(searchParams.get("code"))
        }
    }, [searchParams])
    
  return (
    <LoadingScreen />
  )
}

export default Authorization